import * as React from "react";
import { useUserInfo } from "shared/components/save";
import Defer from "shared/components/ui/Defer";
import ajax, { useFetcher } from 'shared/components/ajax';
import Navbar from "shared/components/ui/Navbar";
import { Button, Checkbox, FormControl, List, ListItem, ListItemSecondaryAction, ListItemText, TextField, withStyles } from "@material-ui/core";
import { navigate } from "gatsby";
import { winalert } from "shared/components/util";
import AddressInput from "shared/components/ui/market/AddressInput";

/**
 * @type {import("@material-ui/core/styles/withStyles").Styles<any, any>}
 */
const styles = theme => ({
    head: {
        display: 'flex',
        alignItems: 'center',
    },
});

const Page = withStyles(styles)(({ classes }) => {
    const user = useUserInfo();
    const store = useFetcher(user && user.features.store && '/market/store/info');
    const [title, setTitle] = React.useState('');
    const [address, setAddress] = React.useState(null);
    const [agree, setAgree] = React.useState(false);

    React.useEffect(() => {
        if (store) {
            winalert('Anda sudah mendaftar sebagai agen toko').then(() => navigate('/store/'));
        }
    }, [store]);

    const save = () => {
        ajax.post('/market/store/register/', {
            address,
            title
        }).then(() => {
            alert('Berhasil disimpan');
            navigate('/store/');
        }).catch(x => {
            alert(x.response?.data.message || x.message);
        });
    }
    return (
        <>
            <Navbar />
            <Defer data={user}>
                {() => (
                    <List>
                        <ListItem>
                            <ListItemText primary="Nama Toko" />
                            <ListItemSecondaryAction>
                                <FormControl style={{ minWidth: '40vw' }}>
                                    <TextField value={title} onChange={(e) => setTitle(e.target.value)} />
                                </FormControl>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <ListItem>
                            <ListItemText primary="Alamat Toko" />
                            <ListItemSecondaryAction>
                                <FormControl style={{ minWidth: '40vw' }}>
                                    <AddressInput value={address} onChange={setAddress} />
                                </FormControl>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <ListItem>
                            <ListItemText primary="Saya menyetujui kepada syarat dan kebijakan platform DASAWARSALINK" />
                            <ListItemSecondaryAction>
                                <FormControl>
                                    <Checkbox checked={agree} onChange={(x) => setAgree(x.target.checked)} />
                                </FormControl>
                            </ListItemSecondaryAction>
                        </ListItem>
                        <ListItem>
                            <ListItemSecondaryAction>
                                <FormControl>
                                   <Button onClick={save} disabled={!(address && title && agree)} color="primary" variant="contained">
                                        Buat Akun Toko
                                   </Button>
                                </FormControl>
                            </ListItemSecondaryAction>
                        </ListItem>
                    </List>
                )}
            </Defer>
        </>
    );
});

export default Page;
